import { useState, useCallback } from 'react';
import useSWR, { mutate } from 'swr';
import {
  Button,
  Container,
  Typography,
  Stack,
  Paper,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  IconButton,
  CircularProgress,
} from '@mui/material';
import { Edit, Delete } from '@mui/icons-material';
import axiosInstance, { endpoints } from 'src/utils/axios';
import ReusableDeletePopover from 'src/components/delete-popup/delete-popup';
import { useSettingsContext } from 'src/components/settings';
import MasterDrawer from 'src/components/drawer';
import ItemForm from './itemsForm'; // ðŸ‘ˆ Create this form like customerForm

const fetcher = (url) => axiosInstance.get(url).then((res) => res.data);

export default function ItemView() {
  const settings = useSettingsContext();
  const [openDrawer, setOpenDrawer] = useState(false);
  const [editItem, setEditItem] = useState(null);
  const [deletePopoverEl, setDeletePopoverEl] = useState(null);
  const [selectedItemId, setSelectedItemId] = useState(null);

  const { data: items, error, isLoading } = useSWR(endpoints.items.list, fetcher); // ðŸ‘ˆ Define this endpoint

  const handleOpenDeletePopover = (event, id) => {
    setDeletePopoverEl(event.currentTarget);
    setSelectedItemId(id);
  };

  const handleCloseDeletePopover = () => {
    setDeletePopoverEl(null);
    setSelectedItemId(null);
  };

  const handleConfirmDelete = () => {
    handleDelete(selectedItemId);
    handleCloseDeletePopover();
  };

  const handleAdd = useCallback(() => {
    setEditItem(null);
    setOpenDrawer(true);
  }, []);

  const handleSaveItem = useCallback(
    async (data) => {
      try {
        let response;

        if (editItem) {
          response = await axiosInstance.put(`/item/update/${editItem._id}`, data);
          const updatedItems = items.map((item) =>
            item._id === editItem._id ? response.data : item
          );
          mutate(endpoints.items.list, updatedItems, false);
        } else {
          response = await axiosInstance.post('/item/add', data);
          const updatedItems = [...items, response.data];
          mutate(endpoints.items.list, updatedItems, false);
        }

        setOpenDrawer(false);
      } catch (err) {
        console.error('Failed to save item:', err);
      }
    },
    [items, editItem]
  );

  const handleDelete = useCallback(
    async (id) => {
      try {
        await axiosInstance.delete(`/item/delete/${id}`);
        const updatedItems = items.filter((item) => item._id !== id);
        mutate(endpoints.items.list, updatedItems, false);
      } catch (err) {
        console.error('Failed to delete item:', err);
      }
    },
    [items]
  );

  if (error) {
    return (
      <Container maxWidth={settings.themeStretch ? false : 'xl'}>
        <Typography variant="h4" mb={2}>Items</Typography>
        <Button variant="contained" onClick={handleAdd}>Add Item</Button>
        <p style={{ color: 'red', fontSize: 20, textAlign: 'center', paddingTop: '150px' }}>
          Failed to load items!
        </p>
      </Container>
    );
  }

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Stack direction="row" justifyContent="space-between" mb={3}>
        <Typography variant="h4">Items</Typography>
        <Button variant="contained" onClick={handleAdd}>
          Add Item
        </Button>
      </Stack>

      <Paper sx={{ width: '100%', overflow: 'auto' }}>
        {isLoading ? (
          <div style={{ display: 'flex', justifyContent: 'center', padding: '20px' }}>
            <CircularProgress />
          </div>
        ) : (
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Item Name</TableCell>
                <TableCell>SKU</TableCell>
                <TableCell>Brand</TableCell>
                <TableCell>Category</TableCell>
                <TableCell align="right">Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {items && items.length > 0 ? (
                items.map((item) => (
                  <TableRow key={item._id}>
                    <TableCell>{item.item_name}</TableCell>
                    <TableCell>{item.sku}</TableCell>
                    <TableCell>{item.brand_name}</TableCell>
                    <TableCell>{item.category}</TableCell>
                    <TableCell align="right">
                      <IconButton
                        color="primary"
                        onClick={() => {
                          setEditItem(item);
                          setOpenDrawer(true);
                        }}
                      >
                        <Edit />
                      </IconButton>
                      <IconButton
                        color="error"
                        onClick={(e) => handleOpenDeletePopover(e, item._id)}
                      >
                        <Delete />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))
              ) : (
                <TableRow>
                  <TableCell colSpan={5} align="center" sx={{ py: 4 }}>
                    <Typography variant="body1" color="text.secondary">
                      No items found. Head towards <strong>Add Item</strong> to add them.
                    </Typography>
                  </TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        )}
      </Paper>

      <MasterDrawer
        open={openDrawer}
        onClose={() => setOpenDrawer(false)}
        title={editItem ? 'Update Item' : 'Add Item'}
      >
        <ItemForm
          initialData={editItem || {}}
          onSubmit={handleSaveItem}
          onClose={() => setOpenDrawer(false)}
        />
      </MasterDrawer>

      <ReusableDeletePopover
        open={deletePopoverEl}
        onClose={handleCloseDeletePopover}
        onConfirm={handleConfirmDelete}
        title="Delete Item?"
        description="Are you sure you want to delete this item?"
      />
    </Container>
  );
}
